/* Search Container */
.search-container {
    min-height: 100vh;
    background: var(--light-green);
}

/* Search Header */
.search-header {
    background: linear-gradient(135deg, var(--primary-color) 0%, var(--accent-color) 100%);
    color: var(--white-color);
    padding: 3rem 0;
    text-align: center;
}

.search-header h1 {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.search-header p {
    font-size: 1.1rem;
    opacity: 0.9;
    margin: 0;
}

/* Search Filters */
.search-filters {
    background: var(--white-color);
    padding: 2rem 0;
    border-bottom: 1px solid var(--border-color);
}

.filters-grid {
    display: grid;
    gap: 2rem;
}

.filter-section {
    background: var(--white-color);
}

.filter-section h3 {
    color: var(--dark-green);
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 1.5rem;
}

/* Main Search Bar */
.main-search .search-bar {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr auto;
    gap: 1rem;
    align-items: end;
}

.search-field {
    display: flex;
    flex-direction: column;
}

.search-field label {
    font-size: 0.9rem;
    font-weight: 500;
    color: var(--text-color);
    margin-bottom: 0.5rem;
}

.search-field input {
    padding: 0.875rem 1rem;
    border: 2px solid var(--border-color);
    border-radius: 8px;
    font-size: 1rem;
    transition: all 0.3s ease;
    background: var(--white-color);
}

.search-field input:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 3px rgba(46, 139, 87, 0.1);
}

.btn-search {
    background: var(--primary-color);
    color: var(--white-color);
    border: none;
    border-radius: 8px;
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    height: fit-content;
}

.btn-search:hover {
    background: var(--accent-color);
    transform: translateY(-2px);
    box-shadow: 0 4px 15px rgba(46, 139, 87, 0.3);
}

/* Advanced Filters */
.advanced-filters {
    border-top: 1px solid var(--border-color);
    padding-top: 2rem;
}

.filters-row {
    display: flex;
    gap: 2rem;
    margin-bottom: 2rem;
}

.filter-group {
    flex: 1;
}

.filter-group label {
    display: block;
    font-size: 0.9rem;
    font-weight: 500;
    color: var(--text-color);
    margin-bottom: 0.5rem;
}

.price-range {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.price-range input {
    flex: 1;
    padding: 0.75rem;
    border: 2px solid var(--border-color);
    border-radius: 8px;
    font-size: 0.9rem;
}

.price-range span {
    color: var(--text-color);
    font-weight: 500;
}

/* Services Grid */
.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
}

.service-checkbox {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.75rem;
    border: 2px solid var(--border-color);
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    background: var(--white-color);
}

.service-checkbox:hover {
    border-color: var(--primary-color);
}

.service-checkbox.active {
    border-color: var(--primary-color);
    background: rgba(46, 139, 87, 0.05);
}

.service-checkbox input[type="checkbox"] {
    display: none;
}

.service-checkbox i {
    font-size: 1.1rem;
    color: var(--text-color);
    width: 20px;
    text-align: center;
}

.service-checkbox.active i {
    color: var(--primary-color);
}

.service-checkbox span {
    font-size: 0.9rem;
    color: var(--text-color);
}

.btn-clear-filters {
    background: transparent;
    color: var(--text-color);
    border: 2px solid var(--border-color);
    border-radius: 8px;
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.btn-clear-filters:hover {
    border-color: var(--danger-color);
    color: var(--danger-color);
}

/* Search Results */
.search-results {
    padding: 3rem 0;
}

.results-header {
    margin-bottom: 2rem;
}

.results-header h2 {
    color: var(--dark-green);
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.results-header p {
    color: var(--text-color);
    font-size: 1rem;
    margin: 0;
}

.results-header strong {
    color: var(--primary-color);
}

/* Loading State */
.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4rem 2rem;
    text-align: center;
}

.spinner {
    width: 50px;
    height: 50px;
    border: 4px solid var(--light-green);
    border-top: 4px solid var(--primary-color);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 1.5rem;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Error State */
.error-state {
    text-align: center;
    padding: 4rem 2rem;
    color: var(--danger-color);
}

.error-state i {
    font-size: 4rem;
    margin-bottom: 1.5rem;
}

.error-state h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
}

.error-state p {
    margin-bottom: 2rem;
    color: var(--text-color);
}

/* Empty State */
.empty-state {
    text-align: center;
    padding: 4rem 2rem;
    color: var(--text-color);
}

.empty-state i {
    font-size: 4rem;
    color: var(--light-green);
    margin-bottom: 1.5rem;
}

.empty-state h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: var(--dark-green);
}

/* Accommodations Grid */
.accommodations-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 2rem;
}

.accommodation-card {
    background: var(--white-color);
    border-radius: 16px;
    overflow: hidden;
    box-shadow: var(--card-shadow);
    transition: all 0.3s ease;
    cursor: pointer;
}

.accommodation-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 40px rgba(46, 139, 87, 0.15);
}

.card-image {
    position: relative;
    height: 220px;
    overflow: hidden;
}

.card-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.accommodation-card:hover .card-image img {
    transform: scale(1.05);
}

.favorite-icon {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: rgba(255,255,255,0.9);
    border-radius: 50%;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.favorite-icon:hover {
    background: var(--white-color);
    transform: scale(1.1);
}

.favorite-icon i {
    color: var(--danger-color);
    font-size: 1.1rem;
}

.card-info {
    padding: 1.5rem;
}

.card-info h3 {
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--dark-green);
    margin-bottom: 0.8rem;
    line-height: 1.3;
}

.location {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-color);
    margin-bottom: 1rem;
    font-size: 0.95rem;
}

.location i {
    color: var(--primary-color);
    font-size: 0.9rem;
}

.rating {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
    font-size: 0.9rem;
    color: var(--text-color);
}

.rating i {
    color: #FFD700;
    font-size: 0.9rem;
}

.services-preview {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.service-icon {
    width: 24px;
    height: 24px;
    background: var(--light-green);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    color: var(--primary-color);
}

.price {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--primary-color);
    margin: 0;
}

/* Pagination */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-top: 3rem;
    padding-top: 2rem;
    border-top: 1px solid var(--border-color);
}

.pagination-btn {
    background: var(--white-color);
    border: 2px solid var(--border-color);
    border-radius: 8px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
}

.pagination-btn:hover:not(:disabled) {
    border-color: var(--primary-color);
    color: var(--primary-color);
}

.pagination-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.pagination-info {
    font-size: 0.9rem;
    color: var(--text-color);
    font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
    .search-header {
        padding: 2rem 0;
    }
    
    .search-header h1 {
        font-size: 2rem;
    }
    
    .search-filters {
        padding: 1.5rem 0;
    }
    
    .main-search .search-bar {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    
    .filters-row {
        flex-direction: column;
        gap: 1rem;
    }
    
    .services-grid {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .accommodations-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    
    .search-results {
        padding: 2rem 0;
    }
}

@media (max-width: 480px) {
    .search-header h1 {
        font-size: 1.75rem;
    }
    
    .services-grid {
        grid-template-columns: 1fr;
    }
    
    .card-info {
        padding: 1rem;
    }
    
    .card-info h3 {
        font-size: 1.1rem;
    }
}

/* Focus States for Accessibility */
.search-field input:focus,
.btn-search:focus,
.service-checkbox:focus,
.btn-clear-filters:focus,
.pagination-btn:focus {
    outline: 2px solid var(--primary-color);
    outline-offset: 2px;
}

/* High Contrast Mode Support */
@media (prefers-contrast: high) {
    .accommodation-card {
        border: 2px solid var(--text-color);
    }
    
    .search-field input {
        border-width: 2px;
    }
    
    .service-checkbox {
        border-width: 2px;
    }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
    .accommodation-card:hover {
        transform: none;
    }
    
    .btn-search:hover,
    .service-checkbox:hover,
    .btn-clear-filters:hover,
    .pagination-btn:hover {
        transform: none;
    }
    
    .card-image img {
        transition: none;
    }
}
